.profilePanel{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
}

.profilePanel,*{
    cursor: pointer;
}

.profileName{
    font-size: 14px;
}

.profileIcon{
    width: 30px;
    height: 30px;
    margin-left: 5% ;
}

.profileOverlayPanel{
    position: absolute;
    top: 100%;
    max-height: 0;
    width: 100%;
    -webkit-transition: all .2s ease-in;
    -o-transition: all .2s ease-in;
    transition: all .2s ease-in;
    background-color:#eee;
    -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
            box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    overflow: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 1;
}

.profilePanel:hover .profileOverlayPanel{
    max-height: 200px;
}




